'use client';

import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { useRouter } from 'next/navigation';
import api from '@/services/api';
import { AxiosError } from 'axios';

// --- Tipos ---
type UserRole = 'CLIENT' | 'ADMIN';

interface AuthData {
  token: string;
  user: {
    id: string;
    name: string;
    email: string;
    role: UserRole;
  };
}

interface AuthContextType {
  user: AuthData['user'] | null;
  isAuthenticated: boolean;
  isAdmin: boolean;
  login: (email: string, password: string) => Promise<void>;
  register: (name: string, email: string, password: string) => Promise<void>;
  logout: () => void;
  loading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

// --- Provider Component ---

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<AuthData['user'] | null>(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  useEffect(() => {
    // Tenta carregar o usuário do localStorage ao iniciar
    const storedToken = localStorage.getItem('authToken');
    const storedUser = localStorage.getItem('authUser');

    if (storedToken && storedUser) {
      try {
        const parsedUser = JSON.parse(storedUser);
        setUser(parsedUser);
      } catch (e) {
        console.error("Erro ao parsear dados do usuário:", e);
        // Limpa dados inválidos
        localStorage.removeItem('authToken');
        localStorage.removeItem('authUser');
      }
    }
    setLoading(false);
  }, []);

  const handleAuthSuccess = (token: string, userData: AuthData['user']) => {
    localStorage.setItem('authToken', token);
    localStorage.setItem('authUser', JSON.stringify(userData));
    setUser(userData);
    // Note: Usamos api.defaults.headers.common['Authorization'] fora do useEffect
    // para que ele seja re-aplicado após o login
  };

  const login = async (email: string, password: string) => {
    try {
      setLoading(true);
      const response = await api.post('/auth/login', { email, password });
      
      const { token, user: userData } = response.data;
      handleAuthSuccess(token, userData);

      // Redireciona para home ou painel
      router.push(userData.role === 'ADMIN' ? '/admin' : '/'); 

    } catch (error) { // <-- Remoção do tipo 'any' aqui
      const axiosError = error as AxiosError<{ error: string }>; // Tipagem para o AxiosError
      const message = axiosError.response?.data?.error || 'Falha no login. Verifique suas credenciais.';
      throw new Error(message);
    } finally {
      setLoading(false);
    }
  };

  const register = async (name: string, email: string, password: string) => {
    try {
      setLoading(true);
      const response = await api.post('/auth/register', { name, email, password });
      
      const { token, user: userData } = response.data;
      handleAuthSuccess(token, userData);
      
      // Cadastro bem-sucedido, redireciona
      router.push('/');

    } catch (error) { // <-- Remoção do tipo 'any' aqui
      const axiosError = error as AxiosError<{ error: string }>; // Tipagem para o AxiosError
      const message = axiosError.response?.data?.error || 'Falha no cadastro.';
      throw new Error(message);
    } finally {
      setLoading(false);
    }
  };

  const logout = () => {
    localStorage.removeItem('authToken');
    localStorage.removeItem('authUser');
    delete api.defaults.headers.common['Authorization'];
    setUser(null);
    router.push('/login');
  };

  const isAuthenticated = !!user;
  const isAdmin = user?.role === 'ADMIN';

  return (
    <AuthContext.Provider value={{ user, isAuthenticated, isAdmin, login, register, logout, loading }}>
      {children}
    </AuthContext.Provider>
  );
}

// --- Hook Customizado ---

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth deve ser usado dentro de um AuthProvider');
  }
  return context;
};